/* Add your CSS styles here */
body{
  background:#09161c;
}

.wrapper {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
}

/* Style your search container, result container, and other elements as needed */
.search-container {
  background-color: #f2f2f2;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Style the search element */
.search-element {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Style the search input */
.form-control {
  width: 70%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Style the search button */
button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

/* Style the search button on hover */
button:hover {
  background-color: #0056b3;
}

/* Style the result container */
.result-container {
  margin-top: 20px;
}

/* Style the result grid */
.result-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

/* Style the search-list-item (individual movie items) */
.search-list-item {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;
  cursor: pointer;
}

/* Style the search-list-item on hover */
.search-list-item:hover {
  transform: scale(1.03);
}

/* Style the search-item-thumbnail (movie poster) */
.search-item-thumbnail img {
  width: 100%;
  height: auto;
}

/* Style the search-item-info (movie title and year) */
.search-item-info {
  padding: 10px;
}

/* Style the movie title */
.search-item-info h3 {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}

/* Style the movie year */
.search-item-info p {
  margin: 0;
  font-size: 14px;
  color: #888;
}
